// import  from ""
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	data() {
		return {
			dataList: [],
			page: 1,
			totalPage: 1,
			status: 'loading',
			sortType: 0,
			keyword: '',
			navList: [{
					title: "销量",
					flag: true
				},
				{
					title: "价格",
					flag: true
				}
			],
			current: 0
		}
	},
	components: {
		uniLoadMore
	},
	onLoad(options) {
		this.keyword = options.id;
		console.log(this.keyword)
		this.loadData();
	},
	onShow() {},
	onHide() {},
	methods: {
		loadData() {
			let parmas = {
				uid: getApp().globalData.uid,
				keyword: this.keyword,
				sortType: this.sortType,
				nowPage: this.page
			}
			console.log(parmas)
			this._reqw.ipost(parmas, "searchList").then(res => {
				res.result == 0 ? (this.totalPage = res.totalPage, this.status = "more", res.dataList != undefined && res.dataList
					.length != 0 ? res.dataList
					.forEach(item => {
						this.dataList.push(item)
					}) : this.status = "noMore") : this.$api.tip(res.resultNote)
			}).catch(err => {})
		},
		details(id) {
			this.$api.navTo(`/pagesA/shopdetails/shopDetails?id=${id}`)
		},
		changeTab(v, k) {
			console.log(1)
			this.current != k ? this.current = k : v.flag = !v.flag;
			switch (k) {
				case 0:
					v.flag ? this.sortType = 0 : this.sortType = 1;
					this.initData();
					break;
				case 1:
					v.flag ? this.sortType = 2 : this.sortType = 3;
					this.initData();
					break;
			}
		},
		initData() {
			this.dataList = [];
			this.page = 1;
			this.totalPage = 1;
			this.status = "loading";
			this.loadData();
		},
		submitSearch() {
			this.$api.navTo('/pagesA/search/search_index')
		}
	},
	//  加载
	onReachBottom() {
		console.log("到底")
		this.status = "loading";
		this.totalPage > this.page ? (this.page += 1, this.loadData()) :
			setTimeout(() => {
				this.status = "noMore"
			}, 30)
	},

}
